// @flow
import React, { useState } from 'react';
import type { Element } from 'react';
import _get from 'lodash/get';
import Popover from '@material-ui/core/Popover';
import Tooltip from '@material-ui/core/Tooltip';
import { makeStyles } from '@material-ui/core/styles';
import type { Offer } from 'types/Offer';
import './Status.css';

const useStyles = makeStyles(theme => ({
  paper: {
    maxWidth: '700px',
  },
}));

type Props = {
  offer: Offer,
};

const Status = ({ offer }: Props): Element<'div'> => {
  const status = _get(offer, ['Step', 'messages', 'buyer']);

  const [anchorEl, setAnchorEl] = useState(null);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);

  const popoverClasses = useStyles();

  return (
    <div className="buyer-offer-status">
      <Tooltip title="Click to read more">
        <button onClick={handleClick}>{`${status}...`}</button>
      </Tooltip>
      <Popover
        classes={popoverClasses}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
      >
        <div className="wire-instruction">
          <p>Payable to: Endpoint Escrow, Inc.</p>
          <p>Receiving Bank: First American Trust, FSB</p>
          <p>
            <span>5 First American Way</span>
            <br />
            <span>Santa Ana, CA 92707</span>
          </p>
          <p>Routing #: 122241255</p>
          <p>Credit Beneficiary: Endpoint Escrow, Inc.</p>
          <p>Account #: 3145220000</p>
          <p>
            NOTICE: Funds from a U.S. Bank: Funds should be wired from a bank
            within the United States.
          </p>
          <p>
            NOTICE: ACH Transfers are NOT wire transfers: An ACH transfer is not
            immediately available funds and requires additional time for
            clearance. An ACH transfer cannot be accepted for an imminent
            closing. Acceptance of ACH transfers are subject to state law.
          </p>
          <p>
            <b>
              BEWARE OF CYBER-CRIME! Our bank wire instructions will not change.
            </b>
          </p>
          <p>
            If you receive an e-mail or any other communication that appears to
            be generated from NILE or Endpoint that contains new, revised or
            altered bank wire instructions, consider it suspect and call our
            office at a number you trust.
          </p>
        </div>
      </Popover>
    </div>
  );
};

export default Status;
